import React from 'react'
import { Link } from 'react-router-dom'
import { assetUrl } from '../utils/constant'
function Header() {
    return (

        <>
        <header>
            <nav className="head-nav">
                <div className="container-lg">
                    <div className="wrapper">
                        <div className="brand-logo">
                            <Link to="/gemstools/"><img src={assetUrl+"images/gemstool-logo.png"} alt="NFTs Grades image" width="180px" height="46px" /></Link>
                        </div>
                        <div className="nav-links">
                            <div className="hemburger">
                                <button >
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </button>
                            </div>
                            <div className="nav-links">
                                <ul className="user-list">
                                    <li>
                                        <Link to="/gemstools/upcomming-nft">Upcoming NFTs</Link>
                                    </li>
                                    <li>
                                        <Link to="/gemstools/submit-nft">Submit NFTs</Link>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div className="mobile-nav">
                            <ul className="mobile-userlist">
                                <li>
                                    <Link to="/gemstools/submit-nft">Submit NFTs</Link>
                                </li>
                                <li>
                                    <Link to="/gemstools/upcomming-nft">Upcoming NFTs</Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </nav>
        </header>
        </>
    )
}

export default Header
