import React from "react";
import { nftAssetUrl } from "../utils/constant";

function CarouselItem({data, ...props}) {
  var nft_images = JSON.parse(data.image);
  return (
    <div className="custom-carasol-min-height">
      <div className="carousel-items">
        <div className="item nft-block">
          <div className="content">
            <div className="row m-0">
              <div className="col-5 nft-info-col">
                <div className="nft-info">
                  <div className="image">
                    <a>
                      <img src={nftAssetUrl+nft_images[0]} alt="Nft profile image" height="90px" width="90px" />
                    </a>
                  </div>
                  <div className="nft-title">
                    <h2 className="highlight">{data.nft_name}</h2>
                  </div>
                  <div className="nft-user">
                    <h3 className="peak">{data.fname}</h3>
                  </div>
                  <div className="nft-redirect-btn">
                    <a href={data.nft_link} target={"_blank"}>
                      <i className="fas fa-external-link-alt"></i> Visit now
                    </a>
                  </div>
                </div>
              </div>
              <div className="col-7 nft-stat-col">
                <div className="nft-stats">
                  <div className="item">
                    <div className="text">
                      <span>% NFTs Sold</span>
                    </div>
                    <div className="stat">
                      <span>{data.popularity || 'NaN'}</span>
                    </div>
                  </div>
                  <div className="item">
                    <div className="text">
                      <span>Social media</span>
                    </div>
                    <div className="stat">
                      <span>{data.community || 'NaN'}</span>
                    </div>
                  </div>
                  <div className="item">
                    <div className="text">
                      <span>Design</span>
                    </div>
                    <div className="stat">
                      <span>{data.originality || 'NaN'}</span>
                    </div>
                  </div>
                  <div className="item">
                    <div className="text">
                      <span>Average</span>
                    </div>
                    <div className="stat">
                      <span>{((parseInt(data.popularity) + parseInt(data.originality) + parseInt(data.community)) / 3).toFixed(2)}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CarouselItem;
